import React from "react";

import { connect } from 'react-redux'; 
import * as selectors from '../store/reducers';
//import * as actions from '../store/actions';

/**
 *   
 */
function HomePage ({user}) 
{
    return (
			<div className="bg">
				<h2 style={{marginTop: '70px', marginLeft: '30px'}}>   Interests!</h2>
				<h4 style={{marginLeft: '30px'}}>
					    ,    <a href='/search/example' style={{fontWeight: '600'}}></a>    <a href={"/user/"+user.id} style={{fontWeight: '600'}}></a>,    
				</h4>
				<img src="images/cool.jpg" style={{marginLeft: '20px'}} alt=""/>
			</div>
    	);
}

const mapStateToProps = state => ({
    user: selectors.getUser(state)
});

  const mapDispatchToProps = () => {
	return {
	}
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePage);
